import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function strongSell(_tableName: string) {
  const title = "Strong Sell"
  return genericColumn({
    endpoint: "companyAnalystRatings",
    columnKeys: ["strongSell"],
    size: 90,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Number of analysts with Strong Sell rating",
    cell: info => {
      const value = info.row.original.financialData.companyAnalystRatings?.data?.strongSell
      return (
        <WatchlistDataTableTextCell
          queryResult={info.row.original.financialData.companyAnalystRatings}
          value={value}
        >
          {value ?? "-"}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { strongSell }
